body {
  margin: 0;
}

.header {
  color: #060b35;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  margin: 0;
  padding: 0 50px;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.header__logo {
  align-items: center;
  width: 40px;
  height: 40px;
  margin-top: 2px;
  display: flex;
}

.nav {
  align-items: center;
  display: flex;
}

.nav__links {
  justify-content: flex-end;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  line-height: 60px;
  list-style: none;
}

.nav__item {
  text-align: center;
  height: 60px;
  margin-left: 17px;
  line-height: 60px;
  display: inline-block;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  text-transform: uppercase;
  cursor: pointer;
  color: #060b35;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.nav .is-active {
  color: #00acdc;
  position: relative;
}

.nav .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.product-card {
  color: #060b35;
  grid-template-rows: repeat(1, 408px);
  grid-template-columns: repeat(1, 200px);
  grid-auto-flow: row;
  place-content: center;
  gap: 46px 48px;
  margin: 0;
  padding: 50px 40px;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: grid;
}

.element {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  transition: transform .3s;
}

.element:hover {
  transform: scale(1.2);
}

.img {
  background-image: url("imac.202c52ff.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 160px 134px;
  width: 160px;
  height: 134px;
  margin: 32px 19px 40px;
}

.info {
  flex-direction: column;
  width: 166px;
  margin: 0 16px 16px;
  display: flex;
}

.name {
  cursor: pointer;
  margin: 0 0 4px;
  line-height: 18px;
}

.name:hover {
  color: #34568b;
}

.code {
  color: #616070;
  margin: 0 0 6px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.rating {
  flex-direction: row;
  align-items: center;
  width: 168px;
  margin-bottom: 14px;
  line-height: 16px;
  display: flex;
}

.star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.star:last-child {
  margin-right: 0;
}

.star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.reviews {
  margin-left: 19px;
  font-size: 10px;
  font-weight: 400;
}

.price {
  flex-direction: row;
  align-items: center;
  width: 168px;
  height: 18px;
  margin-bottom: 16px;
  display: flex;
}

.price-text {
  color: #616070;
  margin-right: 85px;
  font-weight: 400;
}

.price-numbers {
  margin-left: 1px;
  padding-bottom: 1px;
  font-size: 16px;
  font-weight: 700;
}

.button {
  color: #fff;
  cursor: pointer;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  height: 40px;
  font-size: 14px;
  font-weight: 600;
  line-height: 37px;
  display: flex;
}

.button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

@media (width >= 488px) {
  .product-card {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .product-card {
    grid-template-rows: repeat(2, 408px);
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .product-card {
    grid-template-rows: repeat(2, 408px);
    grid-template-columns: repeat(4, 200px);
  }
}
